html {
  font-family: "Roboto", sans-serif;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  outline: none;
}

body {
  min-height: 100vh;
  padding: 20px;
}

label,
p {
  margin: 0;
}

ul {
  padding: 0;
}

li {
  list-style: none;
}

input {
  border: none;
}
